fieldset {
  font-family: inherit;
  font-size: 1rem;
  border: none;
  border-radius: 0;
}
label {
  vertical-align: middle;
}
select {
  -webkit-appearance: none;
}
textarea {
  line-height: 1.75;
  padding: 0.5rem 0.5rem;
  vertical-align: top;
}
.input[type=button],
.input[type=date],
.input[type=datetime],
.input[type=email],
.input[type=file],
.input[type=hidden],
.input[type=image],
.input[type=month],
.input[type=number],
.input[type=password],
.input[type=range],
.input[type=reset],
.input[type=search],
.input[type=submit],
.input[type=tel],
.input[type=text],
.input[type=time],
.input[type=url],
.input[type=week],
.select,
.textarea {
  border-color: rgba(0,0,0,0.1);
  border-style: solid;
  border-width: 1px;
  font-size: 1rem;
  height: auto;
  outline: none;
  padding: 1rem;
  width: 100%;
  transition: border-color 200ms;
}
.input[type=button]:focus,
.input[type=date]:focus,
.input[type=datetime]:focus,
.input[type=email]:focus,
.input[type=file]:focus,
.input[type=hidden]:focus,
.input[type=image]:focus,
.input[type=month]:focus,
.input[type=number]:focus,
.input[type=password]:focus,
.input[type=range]:focus,
.input[type=reset]:focus,
.input[type=search]:focus,
.input[type=submit]:focus,
.input[type=tel]:focus,
.input[type=text]:focus,
.input[type=time]:focus,
.input[type=url]:focus,
.input[type=week]:focus,
.select:focus,
.textarea:focus {
  border-color: #777;
}
.input[required],
.input:invalid,
.input:valid {
  padding-right: 2.5rem;
  text-overflow: ellipsis;
  overflow: hidden;
}
.input:invalid,
.input:valid {
  background-repeat: no-repeat;
  background-size: 8px;
  background-position: right 1rem center;
}
.input.textarea[required],
.input.textarea:invalid,
.input.textarea:valid {
  background-position: right 1rem top 1rem;
}
.input[required] {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 8 16 16" enable-background="new 0 8 16 16"><style type="text/css">.st0{fill:#F18260;}</style><path class="st0" d="M7.2 17.4l-5.4 3.8-1.4-2.5 6-2.7-6.1-2.8 1.4-2.4 5.4 3.8-.5-6.6h2.7l-.5 6.6 5.4-3.8 1.4 2.4-6 2.8 6.1 2.8-1.4 2.5-5.4-3.8.5 6.5h-2.8l.6-6.6z"/></svg>");
}
.input:valid {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><circle fill="#B4D388" cx="8" cy="8" r="8"/></svg>");
}
.input:focus:invalid {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><circle fill="#CF5340" cx="8" cy="8" r="8"/></svg>");
}
.input[novalidate],
.input.novalidate,
.input.hide-validate-icon {
  background-image: none;
  padding-right: 1rem;
}
.input-wrap,
.radio-group,
.checkbox-group,
.fieldset {
  margin-bottom: 2rem;
}
.label {
  display: inline-block;
  margin-bottom: 1rem/2;
}
.fieldset {
  background-color: #f4f4f4;
  border: 1px solid rgba(0,0,0,0.1);
  padding: 1rem 2rem;
}
.input[type=checkbox],
.input[type=radio] {
  display: inlinew;
}
.radio-group,
.checkbox-group {
  border-radius: 3px;
  background-color: #f4f4f4;
  padding: 1rem 1.5rem;
}
.radio-wrap,
.checkbox-wrap {
  display: inline-block;
  margin-right: 1rem;
}
.radio-wrap:last-child,
.checkbox-wrap:last-child {
  margin-right: 0;
}
@media screen and (max-width: 480px) {
  .radio-wrap,
  .checkbox-wrap {
    display: block;
  }
}
.stacked .radio-wrap:not(:last-child),
.stacked .checkbox-wrap:not(:last-child) {
  display: block;
}
.input-group {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.select {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="8" height="11" viewBox="-469 273 8 11" enable-background="new -469 273 8 11"><path fill="#B4B4B4" d="M-469 277l4-4 4 4zM-461 280l-4 4-4-4z"/></svg>");
  background-repeat: no-repeat;
  background-position: right 1rem center;
}
.input[type=color] {
  background-color: #fff;
  cursor: pointer;
  outline: none;
  height: 50px;
  padding: 5px;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
  .input[type=color] {
    padding: 1rem;
  }
}
input[type=color]::-webkit-color-swatch {
  border: none !important;
}
input[type=color]::-webkit-color-swatch-wrapper {
  padding: 0 !important;
}
::-webkit-calendar-picker-indicator,
::-webkit-calendar-picker-indicator:hover {
  background: transparent;
  color: #0ecb2d;
  padding: 0;
  padding-left: 1rem;
}
::-webkit-datetime-edit-fields-wrapper {
  padding: 0;
}
::-webkit-inner-spin-button {
  display: none;
}
.input[type=file] {
  font-size: 0.75rem;
  background-color: #f4f4f4;
  display: block;
}
.input[type=reset] {
  background-color: #f4f4f4;
  border-color: #ccc;
  color: #222;
  cursor: pointer;
}
button,
input[type=submit],
.btn {
  cursor: pointer;
  outline: none;
  white-space: nowrap;
  -webkit-appearance: none;
  color: #fff;
  background: #0ecb2d;
  border: 1px solid transparent;
  font-size: inherit;
  text-align: center;
  line-height: 1;
  height: auto;
  align-self: flex-start;
  display: inline-block;
  border-radius: 3px;
  padding: 1rem;
  transition: box-shadow 200ms;
}
button:hover,
input[type=submit]:hover,
.btn:hover {
  box-shadow: inset 0 0 0 20rem rgba(34,34,34,0.1);
}
button:active,
input[type=submit]:active,
.btn:active {
  transform: translateY(1px);
}
button:disabled,
input[type=submit]:disabled,
.btn:disabled {
  opacity: 0.6;
  transition: none;
  cursor: progress;
}
@media screen and (max-width: 480px) {
  button,
  input[type=submit],
  .btn {
    width: 100%;
  }
}
.btn:hover {
  color: #fff;
}
.btn-large {
  padding: 1.5rem;
}
.btn-secondary {
  background: #777;
}
.btn-outline {
  color: #0ecb2d;
  background-color: transparent;
  border-color: currentColor;
  transition: background 200ms, color 200ms;
}
.btn-outline:hover {
  color: #fff;
  background: #0ecb2d;
  box-shadow: none;
  border-color: transparent;
}
.btn-small {
  padding: 0.5rem;
}
.btn-tiny {
  font-size: 80%;
  padding: 0.25rem;
}
